import React, { Component } from 'react';
import { Row, Col, Card, Divider, Table } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { setListStudentForSummarizePoints } from 'dump/data';

class TableListSummarizeSemester extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H v tn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dayOfBirth',
        key: 'dayOfBirth',
      },
      {
        title: 'Ni sinh',
        dataIndex: 'placeOfBirth',
        key: 'placeOfBirth',
      },
      {
        title: 'TBCHT4',
        dataIndex: 'TBCHT4',
        key: 'TBCHT4',
      },
      {
        title: 'TBCHC10',
        dataIndex: 'TBCHC10',
        key: 'TBCHC10',
      },
      {
        title: 'Xp loi',
        dataIndex: 'rank',
        key: 'rank',
      },
      {
        title: 'S HP n',
        dataIndex: 'SoHPNo',
        key: 'SoHPNo',
      },
      {
        title: 'C hc c s',
        dataIndex: 'CoHocCoSo',
        key: 'CoHocCoSo',
      },
      {
        title: 'ng li cch mng',
        dataIndex: 'DgLoiCachMang',
        key: 'DgLoiCachMang',
      },
      {
        title: 'Hnh ha - V k thut',
        dataIndex: 'HHVeKyThuat',
        key: 'HHVeKyThuat',
      },
      {
        title: 'Nhng nguyn l c bn',
        dataIndex: 'NNLCBCNMacLeNin',
        key: 'NNLCBCNMacLeNin',
      },
      {
        title: 'Sc bn vt liu',
        dataIndex: 'SucBenVL',
        key: 'SucBenVL',
      },
      {
        title: 'T tng H Ch Minh',
        dataIndex: 'TuTuongHCM',
        key: 'TuTuongHCM',
      },
      {
        title: 'Vt liu xy dng',
        dataIndex: 'VatLieuXD',
        key: 'VatLieuXD',
      },
      {
        title: 'V Auto Card',
        dataIndex: 'VeAutocard',
        key: 'VeAutocard',
      },
      // {
      //   title: 'Thao tc',
      //   dataIndex: 'operation',
      //   render: () => (
      //     <Button className="btnSmallDeleteInTable" onClick={this.showModal}>
      //       Xa
      //     </Button>
      //   ),
      // },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }
  render() {
    return (
      <Card>
        <Divider>Tng hp kt qu hc tp ton kha</Divider>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
        <Row>
          <Col>
            <div>
              <Table
                bordered
                rowSelection={this.rowSelection}
                dataSource={setListStudentForSummarizePoints}
                columns={this.columns}
                scroll={{ x: 'true' }}
              />
            </div>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default TableListSummarizeSemester;



