import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import FormInfoCommonFullCourse from './FormInfoCommonFullCourse';
import MenuTabTop from './MenuTabTop';
import TableListSummarizeCourse from './TableListSummarizeCourse';
import './style.scss';

class ListSummarizePointsFullCoursePage extends Component {
  state = {};
  render() {
    return (
      <Card title="Kt qu hc tp">
        <Row className="rowMargin">
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoCommonFullCourse />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableListSummarizeCourse />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSummarizePointsFullCoursePage />
  </Page>
);



