import React, { Component } from 'react';
import { Row, Col, Form, Select, InputNumber, Checkbox } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Hc k:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Nm hc:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Hin th thang im:">
              <Select>
                <Option value="1">Thang im 4</Option>
                <Option value="2">Thang im 10</Option>
              </Select>
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ln thi:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24} className="rowMargin">
          <Col span={8}>
            <p>
              Tng s sinh vin: <b>55</b>
            </p>
          </Col>
          <Col span={8}>
            <p>
              Tng s hc phn: <b>51</b>
            </p>
          </Col>
          <Col span={8}>
            <Checkbox /> Hin th nhiu u im
          </Col>
          <Col span={8}>
            <Checkbox /> Hin th tn hc phn
          </Col>
          <Col span={8}>
            <Checkbox /> Hin th mn chng ch
          </Col>
          <Col span={8}>
            <Checkbox /> Hin th ghi ch im thi
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



